				"html": "<p>These represent finite ordered sets indexed by non-negative numbers. The built-in function <code>len()</code> returns the number of items of a sequence. When the length of a sequence is <code>n</code>, the index set contains the numbers <code>0, 1, ..., n-1</code>. Item <code>i</code> of sequence a is selected by <code>a[i]</code>.</p>\n<p><a href=\"https://docs.python.org/3/reference/datamodel.html#the-standard-type-hierarchy\">Python Docs - Standard Type Hierarchy</a></p>\n<h2>More</h2>\n<ul>\n<li>\n<p>Built-in sequence types are:</p>\n<ul>\n<li>\n<p><code>list</code></p>\n</li>\n<li>\n<p><code>tuple</code></p>\n</li>\n<li>\n<p><code>range</code></p>\n</li>\n</ul>\n</li>\n<li>\n<p>Built-in sequence types are iterable types (implement required <code>__iter__()</code> method).</p>\n</li>\n<li>\n<p>Operations:</p>\n<ul>\n<li>\n<p><a href=\"https://docs.python.org/3/library/stdtypes.html#common-sequence-operations\">Common Sequence Operations</a></p>\n</li>\n<li>\n<p><a href=\"https://docs.python.org/3/library/stdtypes.html#mutable-sequence-types\">Mutable Sequence Types</a></p>\n</li>\n<li>\n<p><a href=\"https://docs.python.org/3/library/stdtypes.html#immutable-sequence-types\">Immutable Sequence Types</a></p>\n</li>\n</ul>\n</li>\n</ul>",
